<template>
	<div class="set_nickname">
		<van-cell-group>
      <van-field :value="areaText" label="" placeholder="" readonly @click="showArea = true"/>
      <van-address-edit-detail
        :focused="detailFocused"
        :value="address_detail"
        :error="!!$vuelidation.error('address_detail')"
        @blur="detailFocused = false"
        @input="onChangeDetail" />
		</van-cell-group>

		<div class="bottom_btn">
			<van-button size="large" type="danger" @click="save"></van-button>
		</div>

    <van-popup v-model="showArea" position="bottom" :lazy-render="false" :get-container="getAreaContainer">
      <van-area
        ref="area"
        :loading="!areaListLoaded"
        :value="area_code"
        :area-list="areaList"
        @confirm="onAreaConfirm"
        @cancel="showArea = false"
      />
    </van-popup>
	</div>
</template>


<script>
	import { USER_PROFILE } from '@/api/user';
  import areaList from '../../../../assets/data/area';

  import{Field, Toast, CellGroup, Cell, Area, Popup, Button} from 'vant'
  import AddressEditDetail from "vant/lib/address-edit/Detail";
  import { isObj } from "vant/lib/utils";

	export default {

		data(){
			return {
        showArea : false,
        detailFocused: false,
        areaList,
        province: '',
        city: '',
        county: '',
        area_code: '',
        address_detail:''
			}
		},

    components:{
      [Cell.name]: Cell,
      [CellGroup.name]: CellGroup,
      [Area.name]: Area,
      [Field.name]: Field,
      [Popup.name]: Popup,
      [Toast.name] : Toast,
      [Button.name]: Button,
      [Popup.name] : Popup,
      [AddressEditDetail.name]: AddressEditDetail
    },

    computed: {
      areaListLoaded() {
        return isObj(this.areaList) && Object.keys(this.areaList).length;
      },

      areaText() {
        return this.province && this.city && this.county && this.area_code
          ? `${this.province} ${this.city} ${this.county}`
          : '';
      },
    },

		vuelidation: {
			data: {
        area_code: {
					required: {
						msg(){
							return ""
						}
					}
				},
        address_detail: {
          required: {
            msg(){
              return ""
            }
          }
        }
			}
		},

		created(){
			this.getArea();
		},

		methods: {
      getAreaContainer() {
        return document.body;
      },
      onChangeDetail(val) {
        this.address_detail = val;
      },
      getArea(){
				this.provide = localStorage.getItem('provide') || '';
				this.city = localStorage.getItem('city') || '';
				this.county = localStorage.getItem('county') || '';
				this.area_code = localStorage.getItem('area_code');
				this.address_detail = localStorage.getItem('address') || '';
			},
			save(){
				if(this.mixValid()){
					this.$reqPut(USER_PROFILE, {provide: this.provide, city: this.city, county : this.county, area_code: this.area_code, address_detail: this.address_detail}).then(res => {
						 return this.$dialog.alert({message: ''})
					}).then(() => {
						this.$router.go(-1);
					})
				}
			},

      onAreaConfirm(values) {
        this.showArea = false;
        if(values[2].code==-1){
          return;
        }
        this.area_code = values[2].code;
        this.assignAreaValues(values);
      },

      assignAreaValues(values) {
        this.province= values[0].name;
        this.city = values[1].name;
        this.county = values[2].name;
      }
		}
	}
</script>


<style scoped>
	.bottom_btn{
		padding: 30px 15px 0 15px;
	}
</style>



